<script>
import vueSmoothScroll from 'vue-smooth-scroll';
import Vue from 'vue';
Vue.use(vueSmoothScroll);

export default {
	name: 'top',
	data: () => {
		return {
			links: [
				{ id: 'top', name: 'TOP' },
				{ id: 'works', name: 'WORKS' },
				{ id: 'about', name: 'ABOUT' },
				{ id: 'skills', name: 'SKILLS' },
			],
			active: false,
		};
	},
	methods: {
		toggleNavClass() {
			if (this.active == false) {
				return 'top__block--relative';
			} else {
				return 'top__block--fixed';
			}
		},
	},
	mounted() {
		window.document.onscroll = () => {
			let fixedheight = window.innerHeight;
			let changeheight = fixedheight * 0.6;
			if (window.scrollY > changeheight) {
				this.active = true;
			} else {
				this.active = false;
			}
		};
	},
};
</script>

